:root {
  --bs-primary: #c5050c;
  --bs-secondary: #585858;
  --bs-warning: #ffae00;
  --bs-success: #5cb85c;
  --bs-gray: #6e6e6e;
  --clr-dark-gray-100: #5b646b;
  --clr-dark-gray: #818181;
  --bs-white: #fefefe;
  --bs-gray-dark: #646464;
  --clr-light-gray: #f3f3f3;
  --clr-light-gray-2: #dddddd;
  --clr-light-gray-3: #f1f1f1;
  --clr-medium-gray: #cacaca;
  --clr-medium-gray-2: #e1e1e1;
  --clr-medium-gray-3: #b7b7b7;
  --clr-disabled: #969696;
  --ff-primary: 'Red Hat Display', sans-serif;
  --ff-secondary: 'Red Hat Text', sans-serif;
  --fs-default: 1rem;
  --default-page-width: 82.5rem;
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    --transition: all 0.3s ease-in-out;
  }
}
@media (prefers-reduced-motion: reduce) {
  :root {
    --transition: none;
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

html,
body {
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  height: 100%;
  background: var(--bs-white);
  color: var(--bs-gray-dark);
  font-family: var(--ff-primary);
}

body {
  position: relative;
  overflow-x: hidden;
}

.sr-only {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

img,
svg {
  max-width: 100%;
  height: auto;
  display: block;
}

.pageHelp,
.merchTop,
.viewfiltersDiv,
.bottomButtons,
.searchCatWrap,
.sortCatWrap {
