/* prettier-ignore */
h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-weight: 600;
  line-height: 1.15;
  letter-spacing: -0.022em;
  margin: 0;
}

/* prettier-ignore */
h1 {
  font-size: 2.25rem;
}

/* prettier-ignore */
h2 {
  font-size: 1.875rem;
}

/* prettier-ignore */
h3 {
  font-size: 1.5rem;
}

/* prettier-ignore */
h4 {
  font-size: 1.125rem;
}

/* prettier-ignore */
h5 {
  font-size: 0.875rem;
}

/* prettier-ignore */
h6 {
  font-size: 0.75rem;
}

a {
  color: var(--bs-primary);
}
a:link, a:visited, a:focus, a:active {
  color: var(--bs-primary);
  text-decoration: none;
  transition: var(--transition);
}
a:hover {
  text-decoration: underline;
}

.heading__line, .heading__line-center {
  display: grid;
  grid-template-columns: 0.625rem max-content 1fr;
  gap: 0.625rem;
  align-items: center;
  border: none;
  padding-block-end: 0;
  padding-inline-start: 0;
  background: none;
  margin: 0;
  margin-block-end: 1.5625rem;
  width: 100%;
  font-size: inherit;
  font-style: italic;
  color: var(--bs-gray-dark);
}
.heading__line::before, .heading__line::after, .heading__line-center::before, .heading__line-center::after {
  content: "";
  display: block;
  height: 0.0625rem;
  background-color: var(--clr-light-gray-2);
}
.heading__line-center, .heading__line-center-center {
  grid-template-columns: 1fr max-content 1fr;
}

.tabs__header {
  text-align: center;
  font-weight: 500;
  font-size: 1rem;
}

.promo-banner {
  position: relative;
  min-height: 2.8125rem;
  background-color: var(--bs-primary);
  color: var(--bs-white);
  text-align: center;
  font-style: italic;
  font-size: 1.125rem;
  padding-block: 0.5rem;
  overflow: clip;
}
.promo-banner strong {
  text-transform: uppercase;
}
.promo-banner .container-fluid {
  position: relative;
  z-index: 2;
}
.promo-banner::after {
  position: absolute;
  bottom: 0px;
  right: 0;
  height: 12.5rem;
  width: 9.375rem;
  content: "";
  opacity: 0.5;
  transform: skew(135deg);
  z-index: 1;
  background-color: rgb(165, 5, 10);
}
@media (min-width: 62em) {
  .promo-banner::after {
    right: 12.5rem;
    width: 12.5rem;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.75rem;
  border: 0.125rem solid transparent;
  border-radius: 0;
  font-weight: 500;
  height: 3.125rem;
  font-size: inherit;
  transition: var(--transition);
}
.btn-primary {
  background-color: transparent;
  border-color: var(--bs-primary);
  color: var(--bs-primary);
}
.btn-primary:hover, .btn-primary:active, .btn-primary:active:hover, .btn-primary:focus, .btn-primary:active:focus, .btn-primary-inverted {
  border-color: var(--bs-primary);
  background-color: var(--bs-primary);
  color: var(--bs-white);
}
.btn-primary-inverted:hover, .btn-primary-inverted:active, .btn-primary-inverted:focus {
  color: var(--bs-white);
}
.btn-success {
  background-color: transparent;
  border-color: var(--bs-success);
  color: var(--bs-success);
}
.btn-success:hover, .btn-success:active, .btn-success:active:hover, .btn-success:focus, .btn-success:active:focus, .btn-success-inverted {
  border-color: var(--bs-success);
  background-color: var(--bs-success);
  color: var(--bs-white);
}
.btn-default {
  border-color: var(--bs-secondary);
  color: var(--bs-secondary);
}
.btn-disabled {
  cursor: not-allowed;
  background-color: var(--clr-disabled);
  border: 0.0625rem solid var(--clr-disabled);
  box-shadow: none;
  color: var(--bs-white);
}
.btn-disabled:link, .btn-disabled:visited {
  color: var(--bs-white);
}
.btn-disabled:hover, .btn-disabled:active, .btn-disabled:active:focus {
  cursor: not-allowed;
  background-color: var(--clr-disabled);
  border: 0.0625rem solid var(--bs-secondary);
  box-shadow: none;
  color: var(--bs-white);
}
.btn-secondary {
  background-color: transparent;
  border-color: var(--bs-secondary);
  color: var(--bs-secondary);
}
.btn-secondary:link, .btn-secondary:visited {
  color: var(--bs-secondary);
}
.btn-secondary:hover, .btn-secondary:active, .btn-secondary:active:focus {
  color: var(--bs-white);
  border-color: var(--bs-primary);
  background-color: var(--bs-primary);
}
.btn-rnd {
  border-radius: 6.25rem;
  padding: 0;
  width: 3.125rem;
}
@media (min-width: 62em) {
  .btn-rnd {
    width: 1.875rem;
  }
}
.btn-rnd i {
  font-size: 2.1875rem;
  padding-top: 0.3125rem;
}
@media (min-width: 62em) {
  .btn-rnd i {
    font-size: 1.125rem;
  }
}
.btn-container button {
  border: none;
  background-color: transparent;
  color: var(--bs-primary);
  border-bottom: 0.0625rem solid var(--bs-primary);
  margin-bottom: 0.625rem;
}
.btn:hover {
  text-decoration: none;
}
@media (min-width: 62em) {
  .btn {
    border-width: 0.0625rem;
    height: 2.5rem;
  }
}

.dropdown {
  background-color: var(--clr-light-gray-3);
}
.dropdown-menu {
  border-radius: 0;
  padding: 0;
  background-color: var(--clr-light-gray-2);
  border: none;
  box-shadow: 0 0.1875rem 0.25rem 0.0625rem rgba(0, 0, 0, 0.25);
}
.dropdown-menu[data-bs-popper] {
  margin: 0;
}
.dropdown-menu [class^=col-] {
  padding: 0;
}
.dropdown-header {
  border-bottom: 0.0625rem solid var(--clr-medium-gray-3);
  padding: 0.875rem;
  font-weight: 600;
  font-family: var(--ff-secondary);
  color: var(--bs-gray-dark);
  text-transform: uppercase;
  font-size: 0.875rem;
}
.dropdown ul {
  list-style: none;
  padding: 0;
}
@media (min-width: 62em) {
  .dropdown {
    background-color: transparent;
  }
  .dropdown:last-child .dropdown-toggle::after {
    content: "";
  }
  .dropdown-toggle::after {
    content: none;
    border: none;
    vertical-align: initial;
    margin-left: 0;
  }
  .dropdown-header {
    border: none;
    padding: 0.3125rem 0;
  }
  .dropdown-menu.my-account {
    right: 0;
    left: auto;
    width: 18.75rem;
    padding: 1.5625rem 0;
  }
  .dropdown-menu.my-account a {
    text-align: right;
    padding: 0.625rem 0.9375rem !important;
  }
  .dropdown-menu {
    width: 100%;
    background-color: var(--clr-light-gray-3);
    padding: 1.5625rem 0.625rem 0;
  }
  .dropdown-menu [class^=col-] {
    padding-inline-start: 1.25rem;
  }
  .dropdown-menu {
    top: 6.25rem !important;
  }
  .dropdown-menu[data-bs-popper] {
    top: 6.25rem;
  }
  .dropdown-menu .nav-link {
    padding: 0.3125rem 0 !important;
    font-size: 0.875rem;
  }
  .dropdown-menu .nav-link.show-all {
    border-top: 0.0625rem solid var(--clr-medium-gray);
    padding: 0.875rem 0 !important;
    margin-block-start: 1.875rem;
  }
}

.dropdown, .dropdown-center, .dropend, .dropstart, .dropup, .dropup-center {
  position: static;
}

/* ============ mobile view ============ */
@media (min-width: 62em) {
  .dropdown:hover > .dropdown-menu {
    display: none;
  }
  .dropdown:hover > .dropdown-menu.show {
    display: block;
  }
}
.footer {
  background-color: var(--bs-primary);
  color: var(--bs-white);
  overflow: hidden;
}
.footer__top {
  position: relative;
  z-index: 30;
  background-color: var(--bs-white);
  padding: 1.5625rem 0;
}
.footer__banner:link, .footer__banner:visited {
  display: block;
  margin: 1.5625rem 0;
  padding: 1.5625rem;
  background: var(--clr-medium-gray-2);
  text-align: center;
  text-transform: uppercase;
  font-style: italic;
  font-weight: 500;
  color: var(--clr-dark-gray-100);
  line-height: 1.25;
}
.footer__banner:hover {
  text-decoration: none;
}
.footer__membership {
  font-style: italic;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
}
.footer__membership-link:hover {
  text-decoration: none;
}
.footer__inner, .footer__bottom {
  margin: 0 auto;
  padding: 2.25rem 2.25rem 0;
  max-width: var(--default-page-width);
}
.footer-menu__title {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.125rem;
  margin: 0;
  margin-block-end: 0.25rem;
}
.footer-menu ul {
  padding: 0;
  margin: 0;
  margin-block-end: 1.25rem;
  list-style: none;
}
.footer-menu ul li {
